import React from 'react';
import { Row, Tooltip } from 'antd';
import { Link } from "react-router-dom";

const Webpack = () => (
  <Row className= "content-row">
    <Row type="flex" justify="center">
      <h1>Webpack : l'outil indispensable</h1>
    </Row>
    <Row type="flex" className="">
      <p>
        Webpack est une librairie de configuration pour les applications JavaScript qui permet d'organiser notre application en modules.
        Nous crivons plusieurs fichiers JS vivant dans leur propre module et importants dans la majorit des cas des dpendances
        internes ou externes. Ces dpendances sont d'autres fichiers JS.
      </p>
      <p>
        Qualifi de  bundler , Webpack va parcourir notre application afin de construire un graphe de dpendance des diffrents
        fichiers JS qu'il va compiler et ensuite gnrer sous un ou plusieurs fichiers qu'on appelle  bundle . Ceci est
        indispensable pour les performances d'une application, car au lieu de charger chacun de vos fichiers JS,
        il n'y aura plus qu'un ou un petit nombre de bundle  prendre ne compte.
      </p>
    </Row>
    <Row type="flex">
      <h2>Un cosystme immense</h2>
    </Row>
    <Row type="flex" className="">
      <p>
        En plus de sa fonction de base du  bundle , Webpack offre des possibilits extrmement intressantes
        comme l'automatisation de tches, la conversion l'ES6 en ES5 car mieux support par les navigateurs ou encore
        de compiler du Sass vers du CSS.
      </p>
      <p>
        Lors du projet Syllabo, j'ai configur le fichier webpack et cela peut vite devenir complexe selon les besoins.
        Par exemple, Syllabo permet le
        <a target="_blank" rel="noopener noreferrer"
        href="https://medium.com/walmartlabs/the-benefits-of-server-side-rendering-over-client-side-rendering-5d07ff2cefe8"> Server Side Rendering (SSR) </a>
        mis en place de faon artisanale. Cela nous permet d'avoir un meilleur contrle et plus de flexibilit.
        Mettre en place le SSR n'est pas chose aise et webpack facilite grandement cette tche.
      </p>
    </Row>
    <Row type="flex">
      <h2>La configuration en production</h2>
    </Row>
    <Row type="flex">
      <p>
        Je n'ai pour le moment pas eu l'occasion de configurer webpack pour bundler mon application de faon
        optimale pour une mise en production. C'est sur ce point que je vais devoir me former, mettre en place
        et ainsi gagner en exprience sur cette comptence. Je risque d'essuyer pas mal d'checs mais je ferai
        face  cette adversit pour avoir une application digne de ce nom en production.
      </p>
    </Row>
    <Row type="flex">
      <h2>Ralisations</h2>
    </Row>
    <Row type="flex" gutter={16}>
      <Link to="/realizations/syllabo" onClick={() => this.handleHrefClick('realizations')}>
        <Tooltip title="Syllabo">
          <img src={""} alt="Syllabo" className="realizations-button"/>
        </Tooltip>
      </Link>
    </Row>
  </Row>
);

export default Webpack;



