import React from 'react';
import { Row, Tooltip } from 'antd';
import { Link } from "react-router-dom";

const Javascript = () => (
  <Row className= "content-row">
    <Row type="flex" justify="center">
      <h1>JavaScript</h1>
    </Row>
    <Row type="flex" className="">
      <p>
        JavaScript est un langage de script lger, orient objet, rfrent au niveau des pages web pour dynamiser
        le contenu en agissant sur le
        <a target="_blank" rel="noopener noreferrer" href="https://developer.mozilla.org/fr/docs/Web/API/Document_Object_Model"> Document Object Model. </a>
        Le langage prend vie dans les annes 90 et sa popularit l'amne  tre utilis au sein d'autres environnements comme
        les serveurs avec <a target="_blank" rel="noopener noreferrer" href="https://nodejs.org/en/about/">Node.js.</a>
      </p>
      <p>
        C'est un langage  objets utilisant le concept de
        <a target="_blank" rel="noopener noreferrer" href="https://fr.wikipedia.org/wiki/Programmation_orient%C3%A9e_prototype"> prototype</a>
        , disposant d'un typage faible et
        dynamique qui permet de programmer suivant plusieurs paradigmes de programmation : fonctionnelle, imprative et oriente objet.
      </p>
      <p>
        Le standard du langage est
        <a target="_blank" rel="noopener noreferrer" href="https://developer.mozilla.org/fr/docs/Web/JavaScript/Language_Resources"> ECMAScript </a>
        dont la sixime version majeure (ES6/ECMAScript 2016) a t finalise et publie le 17 juin 2015.
      </p>
    </Row>
    <Row type="flex">
      <h2>Du JavaScript Vanilla  l'ES6</h2>
    </Row>
    <Row type="flex" className="">
      <p>
        Lors de mon exprience sur le projet ISIDOR, j'ai eu la chance de pouvoir coder intensivement en JavaScript Vanilla,
        c'est--dire du JavaScript pur, sans une librairie comme
        <a target="_blank" rel="noopener noreferrer" href="https://jquery.com/"> JQuery </a> et n'tant pas en ES6.
      </p>
      <p>
        Cela m'a permis de comprendre des concepts cls et bas de niveau du langage. JQuery et ES6 ont tendance  cacher ces
        concepts pour gagner en productivit. Mais combien de dveloppeur cherche un minimum  comprendre la magie qui se cache
        derrire toute cette abstraction ?
      </p>
      <p>
        Mon opinion est qu'il est de notre devoir de prendre du temps afin de faire des recherches qui reprsentent
        un investissement sur le long terme. Les langages et librairies peuvent changer ou voluer mais les concepts rarement.
        En connaissant un ancien concept remplac par un nouveau, on comprend mieux la transition et on prend en main plus vite
        le changement.
      </p>
      <p>
        Avant de commencer  coder en ES6, j'ai utilis le Framework
        <a target="_blank" rel="noopener noreferrer" href="https://angularjs.org/"> AngularJS </a>
        et
        <a target="_blank" rel="noopener noreferrer" href="https://angular.io/"> Angular 5 </a>
        lors du projet Beneylu. Le gain en productivit tait indniable avec une certaine
        facilit d'utilisation des Framework. Il tait intressant de voir les diffrences normes entre AngularJS et Angular 5.
      </p>
      <p>
        J'ai ensuite pris en main l'ES6  travers l'application web Syllabo o nous utilisons
        <a target="_blank" rel="noopener noreferrer" href="https://reactjs.org/"> React </a>,
        <a target="_blank" rel="noopener noreferrer" href="https://redux.js.org/"> Redux </a>,
        <a target="_blank" rel="noopener noreferrer" href="https://redux-saga.js.org/"> Saga </a>et
        <a target="_blank" rel="noopener noreferrer" href="https://nodejs.org/en/about/"> Node.js </a>avec
        <a target="_blank" rel="noopener noreferrer" href="http://expressjs.com/"> Express.js. </a>
        L'ES6 apporte son lot de nouveauts
        ainsi que du sucre de syntaxe comme le string
        <a target="_blank" rel="noopener noreferrer"
        href="https://developer.mozilla.org/fr/docs/Web/JavaScript/Reference/Litt%C3%A9raux_gabarits"> interpolation.</a>
      </p>
    </Row>
    <Row type="flex">
      <h2>Un langage passionnant</h2>
    </Row>
    <Row type="flex">
      <p>
        Le JavaScript me passionne et j'ai ador mes diffrentes interactions avec celui-ci.
        Mon exprience m'a donn des bases solides pour devenir voluer sereinement sur ce langage.
        Je continue ma veille technologique et espre pourvoir transmettre mon savoir  d'autres personnes afin de
        renforcer mes acquis et tout simplement partager.
      </p>
    </Row>
    <Row type="flex">
      <h2>Ralisations</h2>
    </Row>
    <Row type="flex" gutter={16}>
      <Link to="/realizations/itschool" onClick={() => this.handleHrefClick('realizations')}>
        <Tooltip title="It'School">
          <img src={""} alt="It'School" className="realizations-button"/>
        </Tooltip>
      </Link>
      <Link to="/realizations/beneylu" onClick={() => this.handleHrefClick('realizations')}>
        <Tooltip title="Beneylu">
          <img src={""} alt="Beneylu" className="realizations-button"/>
        </Tooltip>
      </Link>
      <Link to="/realizations/syllabo" onClick={() => this.handleHrefClick('realizations')}>
        <Tooltip title="Syllabo">
          <img src={""} alt="Syllabo" className="realizations-button"/>
        </Tooltip>
      </Link>
      <Link to="/realizations/maintenance" onClick={() => this.handleHrefClick('realizations')}>
        <Tooltip title="TMA">
          <img src={""} alt="Tierce Maintenance Applicative" className="realizations-button"/>
        </Tooltip>
      </Link>
    </Row>
  </Row>
);

export default Javascript;



