import React from 'react';
import { Row, Tooltip } from 'antd';
import { Link } from "react-router-dom";

const HtmlCSS = () => (
  <Row className= "content-row">
    <Row type="flex" justify="center">
      <h1>Les incontournables de la programmation web</h1>
    </Row>
    <Row type="flex">
      <h2>Des langages complmentaires</h2>
    </Row>
    <Row type="flex" className="">
      <p>
        HTML (HyperText Markup Language) et CSS (Cascading Style Sheets) sont deux langages de programmation indispensables
        lorsque l'on crer des sites web. Un site web est ce que toute personne voit lorsqu'elle utilise ce qu'on appelle un
        navigateur web dont les plus connus sont : Chrome, Safari, Firefox et Internet Explorer. Le code HTML et CSS
        sont  traduits  par le navigateur qui permet ainsi le rendu d'un site web.
      </p>
      <p>
        Le langage HTML a t invent par Tim Berners-Lee en 1991. Il permet de grer et organiser le contenu d'un site web.
        Nous crivons ce que nous souhaitons voir dans notre page comme la mise en place d'un titre, l'affichage d'une image etc.
        Nous construisons donc le squelette du site web.
      </p>
      <p>
        Le CSS va habiller notre squelette et lui donner forme. Il permet de grer l'apparence de la page web et
        mme permettre quelques petites animations. Le CSS est complmentaire au HTML et prend vie en 1996.
      </p>
      <p>
        Il existe diffrentes versions des langages et actuellement nous sommes  la version 5 pour le HTML et 3 pour le CSS.
      </p>
    </Row>
    <Row type="flex">
      <h2>Mes expriences</h2>
    </Row>
    <Row type="flex" className="">
      <p>
        En tant que dveloppeur web, j'ai utilis ces deux langages dans toutes mes ralisations. Le HTML et CSS sont puissants
        et simples  prendre en main.
      </p>
      <p>
        Lorsque la base de code grandie, l'criture du code CSS peut-tre difficile  maintenir et manque de flexibilit.
        Pour pallier ce problme, nous avons un prprocesseur CSS, un programme qui permet de gnrer dynamiquement des
        fichier CSS. J'ai utilis diffrents prprocesseurs comme
        <a target="_blank" rel="noopener noreferrer" href="http://lesscss.org//"> LESS </a>et
        <a target="_blank" rel="noopener noreferrer" href="https://sass-lang.com/"> Sass </a>
        pour les projets Beneylu et Syllabo.
      </p>
    </Row>
    <Row type="flex">
      <h2>Des langages simples mais complexes</h2>
    </Row>
    <Row type="flex">
      <p>
        Bien que les langages soient simples  prendre en main, il ne faut pas les sous-estimer.
        Cette facilit cache en ralit normment de complexits et de concepts qu'il faut prendre le temps de d'analyser.
        Cette comprhension est ncessaire pour par exemple optimiser ou rendre accessible un site pour des personnes sous handicap.
      </p>
      <p>
        Malgr mon utilisation intensive de ces deux langages, je suis loin d'avoir appris et rencontr tous leurs aspects.
        Le web volue rapidement et l'accessibilit du web dans le monde demande d'avoir un site original pour capter l'attention
        des utilisateurs. Nous repoussons donc  chaque fois les limites de ce que peut nous offrir ces deux langages.
      </p>
    </Row>
    <Row type="flex">
      <h2>Ralisations</h2>
    </Row>
    <Row type="flex" gutter={16}>
      <Link to="/realizations/itschool" onClick={() => this.handleHrefClick('realizations')}>
        <Tooltip title="It'School">
          <img src={""} alt="It'School" className="realizations-button"/>
        </Tooltip>
      </Link>
      <Link to="/realizations/beneylu" onClick={() => this.handleHrefClick('realizations')}>
        <Tooltip title="Beneylu">
          <img src={""} alt="Beneylu" className="realizations-button"/>
        </Tooltip>
      </Link>
      <Link to="/realizations/syllabo" onClick={() => this.handleHrefClick('realizations')}>
        <Tooltip title="Syllabo">
          <img src={""} alt="Syllabo" className="realizations-button"/>
        </Tooltip>
      </Link>
      <Link to="/realizations/maintenance" onClick={() => this.handleHrefClick('realizations')}>
        <Tooltip title="TMA">
          <img src={""} alt="Tierce Maintenance Applicative" className="realizations-button"/>
        </Tooltip>
      </Link>
    </Row>
  </Row>
);

export default HtmlCSS;



