import React from 'react';
import { Row, Tooltip } from 'antd';
import { Link } from "react-router-dom";
import persevere from '../../persevere.png';
import integrity from '../../integrity.png';
import empathy from '../../empathy.png';
import teamwork from '../../teamwork.png';
import failure from '../../failure.png';

const Alpha = () => (
  <Row className= "content-row">
    <Row type="flex" justify="center">
      <h1>ALPHA</h1>
    </Row>
    <Row type="flex" className="">
      <h2>Contexte</h2>
    </Row>
    <Row type="flex" className="">
      <p>
        Le projet ministriel Alpha doit mettre en place un systme d'information(SI) achats uniques pour l'ensemble
        des entits achats du Ministre de la Dfense. Sa mise en uvre se prsente en trois objectifs : l'harmonisation
        des SI achats existants sous la forme d'un SI unique, la simplification et l'unification des processus achats,
        l'interoprabilit et partages des bonnes pratiques.
      </p>
      <p>
        En mars 2015, le SI Alpha est confi  Oalia. L'diteur franais Oalia, accompagn des socits de conseils
        IBM et Klee Group ont gagn le projet Alpha.
      </p>
      <p>
        Ce projet se traduit par la conception ainsi que la ralisation de l'application Alpha qui permet la gestion
        des achats au sein du ministre. Cette application est dveloppe en collaboration avec le cotraitant IBM
        et le sous-traitant Oalia.
      </p>
      <p>
        Alpha est suivi de prs par les dirigeants de Klee Group car les enjeux pour Klee ainsi que le
        Ministre de la Dfense sont trs importants.
      </p>
    </Row>
    <Row type="flex">
      <h2>Missions</h2>
    </Row>
    <Row type="flex" className="">
      <p>
      Au sein de l'quipe, je dveloppais les fonctionnalits demandes en customisant le progiciel de la socit Oalia.
      Je devais respecter les documents de spcifications et le plan de charge prvu.
      </p>
      <p>

      </p>
      <p>

      </p>
      <p>

      </p>
      <p>

      </p>
    </Row>
    <Row type="flex">
      <h2>Title</h2>
    </Row>
    <Row type="flex">
      <p>

      </p>
      <p>

      </p>
    </Row>
    <Row type="flex">
      <h2>Comptences</h2>
    </Row>
    <Row type="flex" gutter={16}>
      <Link to="/skills/persevere" onClick={() => this.handleHrefClick('skills')}>
        <Tooltip title="Persvrer">
          <img src={persevere} alt="Persvrer" className="skills-button"/>
        </Tooltip>
      </Link>
      <Link to="/skills/integrity" onClick={() => this.handleHrefClick('skills')}>
        <Tooltip title="Intgrit">
          <img src={integrity} alt="Intgrit" className="skills-button"/>
        </Tooltip>
      </Link>
      <Link to="/skills/empathy" onClick={() => this.handleHrefClick('skills')}>
        <Tooltip title="Empathie">
          <img src={empathy} alt="Empathie" className="skills-button"/>
        </Tooltip>
      </Link>
      <Link to="/skills/teamwork" onClick={() => this.handleHrefClick('skills')}>
        <Tooltip title="Travail d'quipe">
          <img src={teamwork} alt="Travail d'quipe" className="skills-button"/>
        </Tooltip>
      </Link>
      <Link to="/skills/failure" onClick={() => this.handleHrefClick('skills')}>
        <Tooltip title="Echec">
          <img src={failure} alt="Echec" className="skills-button"/>
        </Tooltip>
      </Link>
    </Row>
  </Row>
);

export default Alpha;



