import React from 'react';
import { Row, Tooltip } from 'antd';
import { Link } from "react-router-dom";

const Practice = () => (
  <Row className= "content-row">
    <Row type="flex" justify="center">
      <h1>BBD et TTD pour un produit durable et de qualit</h1>
    </Row>
    <Row type="flex" className="">
      <p>
        Suites  mes diffrentes expriences en TMA, je me suis aperu de l'importance des tests lorsque l'on
        dveloppe une application, notamment dans la dure. Cette pratique est devenue indispensable lorsque je rejoins un projet.
      </p>
      <p>
        C'est notamment sur le projet Syllabo ou je ralise enfin ce rve de crer une application o les tests
        sont obligatoires. Et au fur et  mesure que la base de code grandie, les intrts de cette pratique prend tout son
        sens en gardant l'application cohrente o l'on peut ajouter de la forte valeur ajoute sans s'inquiter des consquences.
      </p>
      <p>
        Nous adoptons la mthodologie Kanban pour avancer dans nos objectifs. Nous pratiquons le
        Behavior Driven Development (BDD) ainsi que Test Driven Development (TDD).
      </p>
      <p>
        Le BDD permet de dtailler le comportement d'une fonctionnalit telle une histoire afin d'identifier
        prcisment o l'on veut aller lorsque l'on code. Cette dfinition de la fonctionnalit se fait en quipe.
      </p>
      <p>
        Nous couplons cela avec le TDD, qui permet de tester les comportements identifis lors de la phase BDD. La particularit
        de la dmarche TDD est qu'elle repose sur 3 principes fondamentaux. Le test doit en premier lieu chouer, ne doit
        pas tre plus compliqu que ncessaire et coder juste assez pour que le test n'choue plus.
        Ces 3 rgles font partie du cycle
        <a target="_blank" rel="noopener noreferrer"
        href="https://msdn.microsoft.com/en-us/library/aa730844(v=vs.80).aspx"> red-green-refactor.</a>
      </p>
      <p>
        Une fois le test fini, la fonctionnalit sous test est insre dans la base de code de l'application.
        L'avantage est que lorsque son comportement changera, le test chouera.
      </p>
      <p>
        Sur Syllabo, nous utilisons
        <a target="_blank" rel="noopener noreferrer" href="https://facebook.github.io/jest/"> Jest </a>
        pour nos tests,
        <a target="_blank" rel="noopener noreferrer" href="https://airbnb.io/enzyme/"> Enzyme </a>
        pour l'accompagner sur les tests de composants React.
        Enfin, nous utilisons
        <a target="_blank" rel="noopener noreferrer" href="https://cucumber.io/"> Cucumber </a>
        pour les tests fonctionnels.
      </p>
    </Row>
    <Row type="flex">
      <h2>Perspectives</h2>
    </Row>
    <Row type="flex" className="">
      <p>
        Ayant enfin trouv un projet qui mettait en avant une philosophie de test, je ne peux plus m'imaginer
        dvelopper une application sans. Les tests ne sont pas si simples  mettre en place et demande de la pratique
        pour tre vraiment efficace. Il me reste encore donc du travail afin de devenir un codeur de tests aguerri.
      </p>
    </Row>
    <Row type="flex">
      <h2>Ralisations</h2>
    </Row>
    <Row type="flex" gutter={16}>
      <Link to="/realizations/syllabo" onClick={() => this.handleHrefClick('realizations')}>
        <Tooltip title="Syllabo">
          <img src={""} alt="Syllabo" className="realizations-button"/>
        </Tooltip>
      </Link>
    </Row>
  </Row>
);

export default Practice;



