import React from 'react';
import { Row, Tooltip } from 'antd';
import { Link } from "react-router-dom";

const Database = () => (
  <Row className= "content-row">
    <Row type="flex" justify="center">
      <h1>Les bases de donnes</h1>
    </Row>
    <Row type="flex" className="">
      <p>
        Une base de donnes permet de stocker des informations de faon organises que l'on peut consulter, grer et mettre  jour.
      </p>
      <p>
        Nous utilisons un Systme de Gestion de Base de Donnes (SGBD) qui est un logiciel qui permet d'interagir avec une
        base donne afin d'y faire nos oprations.
      </p>
      <p>
        Il existe plusieurs types de base de donnes et aujourd'hui les plus utilises sont le SQL, NoSQL et cloud.
        Mes diffrentes expriences m'ont amen  utiliser les BDD SQL et NoSQL.
      </p>
    </Row>
    <Row type="flex">
      <h2>Base de donnes SQL dit relationnelle</h2>
    </Row>
    <Row type="flex" className="">
      <p>
        Ce type de base de donnes a t invent en 1970 par Edgar Frank Codd de IBM.
      </p>
      <p>
        J'ai utilis dans le cadre de mes tudes le SGBDR
        <a target="_blank" rel="noopener noreferrer" href="https://www.mysql.com/fr/"> MySQL </a>
        , surement le plus connu.
        Puis lors de mes projets chez Klee Group j'ai utilis
        <a target="_blank" rel="noopener noreferrer" href="https://www.postgresql.org/"> PostgreSQL </a>
        puis<a target="_blank" rel="noopener noreferrer" href="https://mariadb.org/"> MariaDB </a> en dveloppant
        l'application Beneylu.
      </p>
      <p>
        Ces SGBDR sont des systmes libres, c'est--dire libre et gratuit d'utilisation qu'on oppose aux systmes
        propritaires comme
        <a target="_blank" rel="noopener noreferrer" href="https://www.oracle.com/fr/database/index.html"> Oracle Database.</a>.
      </p>
      <p>
        L'API standard pour les bases de donnes relationnelles est le Structured Query Language (SQL).
        Ce langage est un standard et permet de donner des instructions au SGBDR afin de faire des oprations sur la base de donnes.
        Il est donc possible d'crire un script SQL pouvant tre rutilis par les diffrents SGBDR mme si chaque logiciel
        possde quelques variantes.
      </p>
    </Row>
    <Row type="flex">
      <h2>Base de donnes NoSQL</h2>
    </Row>
    <Row type="flex">
      <p>
        L'utilisation des ces deux types de base a enrichi mon exprience et ma vision du stockage de donnes.
        Cela me permet mieux comprendre les enjeux et de donner mon opinion lorsqu'il se pose la question du choix entre une
        base SQL ou NoSQL.
      </p>
      <p>
        Si une entreprise doit analyser d'importantes quantits de donnes non structures, ou des donnes stockes sur
        plusieurs serveurs cloud virtuels, le NoSQL est idale. Avec l'essor du Big Data, les bases de donnes NoSQL sont de plus
        en plus privilgies.
      </p>
      <p>
        J'ai dcouvert
        <a target="_blank" rel="noopener noreferrer" href="https://redis.io/"> Redis </a>lorsque j'ai travaill sur Beneylu et
        <a target="_blank" rel="noopener noreferrer" href="https://www.mongodb.com/"> MongoDB </a>avec le projet Syllabo.
      </p>
    </Row>
    <Row type="flex">
      <h2>Retour d'expriences</h2>
    </Row>
    <Row type="flex">
      <p>
        L'utilisation des ces deux types de base a enrichi mon exprience et ma vision du stockage de donnes.
        Cela me permet mieux comprendre les enjeux et de donner mon opinion lorsqu'il se pose la question du choix
        entre une base SQL ou NoSQL.
      </p>
      <p>
        Je ne pense pas qu'il ait une meilleure BDD qu'une autre. Chaque type de BDD prsente ces avantages et
        contraintes pour rpondre  un besoin spcifique. Il est de notre devoir de s'informer afin d'aider ou de
        prendre la dcision finale parmi toutes les propositions.
      </p>
      <p>
        Mes diverses expriences m'ont donn la capacit  prendre en main et agir efficacement sur les BDD SQL et NoSQL.
        J'ai encore beaucoup  apprendre concernant les BDD et cela me motive  creuser petit  petit dans ce vaste domaine.
      </p>
    </Row>
    <Row type="flex">
      <h2>Ralisations</h2>
    </Row>
    <Row type="flex" gutter={16}>
      <Link to="/realizations/itschool" onClick={() => this.handleHrefClick('realizations')}>
        <Tooltip title="It'School">
          <img src={""} alt="It'School" className="realizations-button"/>
        </Tooltip>
      </Link>
      <Link to="/realizations/beneylu" onClick={() => this.handleHrefClick('realizations')}>
        <Tooltip title="Beneylu">
          <img src={""} alt="Beneylu" className="realizations-button"/>
        </Tooltip>
      </Link>
      <Link to="/realizations/syllabo" onClick={() => this.handleHrefClick('realizations')}>
        <Tooltip title="Syllabo">
          <img src={""} alt="Syllabo" className="realizations-button"/>
        </Tooltip>
      </Link>
      <Link to="/realizations/alpha" onClick={() => this.handleHrefClick('realizations')}>
        <Tooltip title="Alpha">
          <img src={""} alt="Alpha" className="realizations-button"/>
        </Tooltip>
      </Link>
      <Link to="/realizations/maintenance" onClick={() => this.handleHrefClick('realizations')}>
        <Tooltip title="TMA">
          <img src={""} alt="Tierce Maintenance Applicative" className="realizations-button"/>
        </Tooltip>
      </Link>
    </Row>
  </Row>
);

export default Database;



